
.bankpay .info {
  font-size: 6px;
}
[data-dpr="1"] .bankpay .info, .bankpay [data-dpr="1"] .info {
  font-size: 8px;
}
[data-dpr="2"] .bankpay .info, .bankpay [data-dpr="2"] .info {
  font-size: 16px;
}
[data-dpr="3"] .bankpay .info, .bankpay [data-dpr="3"] .info {
