}
.bankpay_icon {
  background: url(../../../assets/bankforbank.png) no-repeat;
  background-size: contain;
  width: 0.75rem;
  height: 0.5rem;
  display: inline-block;
  position: relative;
  top: 0.18rem;
  right: 0.1rem;
}
.bankpay {
  margin-left: 0.46rem;
  margin-top: 0.46rem;
}
.bankpay input {
    width: 0.28rem;
    height: 0.28rem;
}
