.bankpay .info span {
      display: inline-block;
      width: 0.25rem;
      height: 0.25rem;
      color: #fff;
      text-align: center;
      border-radius: 100%;
      background-color: #238cbb;
      margin-right: 0.156rem;
}
.bankchoices {
  margin-top: 0.42rem;
  margin-bottom: 0.3rem;
  margin-left: 0.45rem;
  width: 4.5rem;
  height: 0.7rem;
  padding-left: 0.3rem;
  border: 1px solid #000;
  color: #898989;
}
.input_l {
  float: left;
}
.tip_r {
  float: right;
  margin-right: 0.64rem;
}



