
.secTip {
  font-size: 8px !important;
}
[data-dpr="1"] .secTip {
  font-size: 8px !important;
}
[data-dpr="2"] .secTip {
  font-size: 18px !important;
}
[data-dpr="3"] .secTip {
  font-size: 30px !important;
}
.layContent {
  font-size: 12px;
}
[data-dpr="1"] .layContent {
  font-size: 8px;
}
[data-dpr="2"] .layContent {
  font-size: 24px;
}
[data-dpr="3"] .layContent {
  font-size: 38px;
}
.binBtn {
  margin: 0 auto;
}
.layContent, .secTip {
  box-sizing: border-box;
}
.secTip {
  position: relative;
}
.sec_close {
  width: 0.96rem;
  height: 0.91733rem !important;
  line-height: 1.024rem;
  border-radius: 100%;
  color: #fff;
  text-align: center;
  background-color: #e4a941;
  position: absolute;
  top: -0.4rem;
  right: -0.3rem;
}
#overlayer {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.25);
  position: fixed;
  z-index: 9999;
}
.layContent {
  width: 10.66667rem;
  background-color: #fff;
  position: relative;
  margin: 0 auto;
  padding-top: 0.8rem;
  padding-bottom: 0.5rem;
  box-shadow: 0rem 0.18rem 0.35rem rgba(0, 0, 0, 0.25);
  top: 5rem;
}
.cen_bind {
  width: 80%;
  margin: 0 auto;
  text-align: left;
}
.cen_bind li:first-child {
    line-height: 0.85333rem;
}
.cen_bind li {
    margin-bottom: 0.3rem;
    height: 1.06667rem;
    line-height: 0.7rem;
}
.cen_bind li label {
      line-height: 0.5rem;
}
.cen_bind li input {
      float: right;
      border: 1px solid #dedede;
      width: 4.26667rem;
      height: 0.74667rem;
      display: block;
}
.cen_bind li select {
      float: right;
      width: 3.55rem;
      height: 0.6rem;
}
.binBtn {
  margin-top: 0.5rem;
  width: 1.92rem;
  line-height: 0.74667rem;
  background-color: #e4a941;
  color: #fff;
  border-radius: 0.1rem;
  text-align: center;
}
.secTip {
  height: 0.5rem !important;
  margin: 0 !important;
  top: -0.45rem;
  text-align: right;
  color: #ea4558;
}



