
.login .input {
  font-size: 12px;
}
[data-dpr="1"] .login .input, .login [data-dpr="1"] .input {
  font-size: 8px;
}
[data-dpr="2"] .login .input, .login [data-dpr="2"] .input {
  font-size: 24px;
}
[data-dpr="3"] .login .input, .login [data-dpr="3"] .input {
  font-size: 38px;
}
.toRegister {
  font-size: 16px;
}
[data-dpr="1"] .toRegister {
  font-size: 16px;
}
[data-dpr="2"] .toRegister {
  font-size: 28px;
}
[data-dpr="3"] .toRegister {
  font-size: 48px;
}
.passItem {
  background-color: #fff;
}
.passItem p:last-child {
    border-bottom: none;
}
.passItem p {
    margin: 0 auto;
    display: table;
    padding: 0.32rem 0;
    height: 2.13333rem;
    border-bottom: 1px solid #dbdbdb;
}
.passItem p svg {
      width: 0.81067rem;
      height: 0.81067rem;
      position: relative;
      top: 0.2rem;
      color: #000;
}
.login .input {
  display: table-cell;
  padding-left: 0.5rem;
  position: relative;
  width: 6.4rem;
  height: 1.28rem;
  line-height: 1.28rem;
  margin-left: 0.32rem;
  border: none;
}
.login .input ::-webkit-input-placeholder {
    color: #a9a9a9;
}
.passInter {
  margin-top: 0.4rem;
}
.toRegister {
  width: 8.6rem;
  height: 1.21875rem;
  outline: none;
  border: 2px solid #2a8fbd;
  border-radius: 0.2rem;
  background: none;
  color: #2a8fbd;
}



