
.main_layer {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  z-index: 100;
}
.main_layer .wrap {
    display: -webkit-flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.main_layer .login {
    top: 8rem;
    background-color: rgba(0, 0, 0, 0.14) !important;
    width: 3rem;
    padding: 0.5rem 0.5rem 0.1rem 0.5rem !important;
    margin: 0 !important;
}
.main_layer .tips, .main_layer .tips_2 {
    background: rgba(0, 0, 0, 0.4);
    color: #fff;
    position: relative;
    z-index: 200;
    padding: 0 1rem;
    margin: 0 auto;
    line-height: 1.5rem;
    top: 12rem;
    border-radius: 0.18rem;
    letter-spacing: 0.05rem;
}



