
.tip {
  font-size: 8px !important;
}
[data-dpr="1"] .tip {
  font-size: 8px !important;
}
[data-dpr="2"] .tip {
  font-size: 18px !important;
}
[data-dpr="3"] .tip {
  font-size: 30px !important;
}
.regMod .modItem input {
  font-size: 12px;
}
[data-dpr="1"] .regMod .modItem input, .regMod .modItem [data-dpr="1"] input {
  font-size: 8px;
}
[data-dpr="2"] .regMod .modItem input, .regMod .modItem [data-dpr="2"] input {
  font-size: 24px;
}
[data-dpr="3"] .regMod .modItem input, .regMod .modItem [data-dpr="3"] input {
  font-size: 38px;
}
.label {
  font-size: 10px;
}
[data-dpr="1"] .label {
  font-size: 14px;
}
[data-dpr="2"] .label {
  font-size: 26px;
}
[data-dpr="3"] .label {
  font-size: 42px;
}
.regMod .modItem input {
  float: left;
}
.regMod {
  box-sizing: border-box;
}
.regvic {
  width: 2.77333rem;
  height: 1.17333rem;
  position: relative;
  top: 0.3rem;
  right: 0rem;
}
.regMod {
  padding-left: 1.70667rem;
  padding-right: 1.70667rem;
  width: 100%;
  background-color: #fff;
}
.regMod form {
    padding-top: .5rem;
}
.regMod .modItem {
    position: relative;
    display: table;
    padding: 0.256rem 0;
    width: 100%;
    height: 2.13333rem;
    border-bottom: 1px solid #dbdbdb;
}
.regMod .modItem input {
      width: 6.82667rem;
      height: 1.06667rem;
      position: relative;
      top: 0.3rem;
      border: none;
      padding-left: 0.3rem;
}
.regMod .modItem ::-webkit-input-placeholder {
      color: #a9a9a9;
}
.label {
  display: table-cell;
  width: 3.2rem;
  text-align: left;
  line-height: 1.23rem;
  letter-spacing: 0.01rem;
  color: #000;
  text-align: left;
  padding-left: 0.16rem;
}
.r_btn {
  text-align: center;
  height: 1.14rem;
  line-height: 1.14rem;
  border-radius: .08rem;
}
.tip {
  width: 100%;
  position: absolute;
  top: 1.92rem;
  left: 3.41333rem;
  color: #886426;
  text-align: left;
}
.tip span {
    display: block;
    float: left;
    width: 0.29867rem;
    height: 0.29867rem;
    line-height: 0.256rem;
    background-color: #886426;
    text-align: center;
    border-radius: 100%;
    margin-right: 0.17067rem;
    position: relative;
    top: 0.10667rem;
}
.tip span svg {
      color: #fff;
      width: 0.17067rem;
      height: 0.17067rem;
}
.showerror {
  background: url("../assets/confirm.png") no-repeat;
  height: 1.17333rem;
  width: 1.10933rem;
  background-size: 150%;
  position: absolute;
  right: 0;
  top: 0.53333rem;
}
.trueH {
  background-position: 0.21333rem center;
}
.errorH {
  background-position: -0.74667rem center;
}



